@import './utils/breakpoints.scss';
@import './utils/fonts.scss';
@import './utils/mixins.scss';
@import './utils/reset.scss';
@import './utils/vars.scss';
// @import './utils/iconmoon.scss';
// @import './../../pug/components/components.scss';

body {
  font-family: $default-font;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  color: #161616;
  background-color: #000;
}

.container {
  max-width: 1500px;
  width: 100%;
  margin: 0 auto;
  padding: 0 16px;
  background-color: #364352;
}

.item-list {
  display: flex;
  flex-wrap: wrap;
  column-gap: 16px;
  align-content: stretch;

  &__item-header {
    display: flex;
    column-gap: 16px;
    align-items: center;
    font-size: 24px;
  }

  &__item-image {
    max-width: 300px;
    width: 100%;
    height: 100px;
    position: relative;
    clip-path: polygon(0 0, 55% 0, 100% 100%, 0% 100%);

    & img {
      position: absolute;
      inset: 0 0 0 0;
      width: 100%;
      height: 100%;
      object-fit: cover;
    } 
  }

  &__item-title {
    transform: translateX(-40px);
  }

  &__item {
    flex: 0 1 calc(50% - 8px);
  }

  &__components {
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
    column-gap: 32px;
  }

  &__components-item {
    flex-basis: 50%;
    padding: 8px 12px;
  }

  &__components-sublist {

  }

  &__components-sublist-item {
    padding: 8px 12px;
  }
}

.relic-data-list {
  display: flex;
  column-gap: 8px;
  align-items: center;

  &__item {
    flex-basis: 10%;

    &:nth-child(3), &:nth-child(4) {
      flex-basis: 20%;
    }

    &:nth-child(5) {
      flex-basis: 40%;
    }
  }
}

.item {

  &__header {
    font-size: 24px;
    margin-bottom: 16px;
  }
}

.component-list {
  padding-left: 32px;

  &__sublist {
    padding-left: 48px;
    display: flex;
    flex-direction: column;
  }

  &__sublist-item {
    display: flex;
    flex-direction: column;
    row-gap: 8px;
  }
}

.item-info-list {
  display: flex;
  column-gap: 16px;
  align-items: flex-start;

  &__item {
    display: flex;
    column-gap: 16px;
    border: solid 1px black;
    padding: 12px 16px;
    flex: 1 0;
    
    &:nth-child(3) {
      flex: 2 0;
    }

    &:nth-child(4), &:nth-child(5) {
      flex: 5 0;
    }

    &_header {
      font-weight: 600;
      font-size: 18px;
      text-transform: uppercase;
    }
  }
}